@media (max-width: 991px) {
    .navbarmain .navbar .container .navbar-collapse .navbar-nav {
        display: none !important;
    }

    .navbarmain .navbar .container {
        display: flex !important;
        flex-wrap: nowrap;
        align-items: center;
        width: 100%;
    }

    .navbarmain .navbar .container .buttonnav {
        display: none;
    }

    .navbarmain .navbar .container .offcanvasbutton {
        display: block;
    }
}